.wrapper {
  // position: relative;
  width: 1100px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  // margin-top: 120px;
  justify-content: center;
  background-color: aliceblue;
  padding: 30px;
  border-radius: 6px;
  box-shadow: inset -5px -5px 5px #fff, inset 5px 5px 7px #fff;
}
.title {
  text-align: center;
}
.content {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media only screen and (max-width: 768px) {
  .wrapper {
    width: 100%;
  }
  .content {
    display: flex;
    flex-direction: column;
    justify-items: center;
  }
}
